"use client";

import { useState, useEffect } from "react";
import { addMateri, deleteMateri, updateMateri } from "@/lib/data"; // pastikan fungsi ini dibuat di lib/data
import { useRouter } from "next/navigation";

export default function MateriList({ initialMateri }) {
  const router = useRouter();
  const [materi, setMateri] = useState(initialMateri);
  const [form, setForm] = useState({ judul: "", isi: "", tanggal: "" });
  const [isAdmin, setIsAdmin] = useState(false);

  useEffect(() => {
    const role = localStorage.getItem("role");
    setIsAdmin(role === "admin");
  }, []);

  // Tambah Materi
  const handleAdd = async (e) => {
    e.preventDefault();
    if (!isAdmin) return;

    const newMateri = await addMateri(form);

    setMateri([...materi, { id: newMateri.id, ...form }]);
    setForm({ judul: "", isi: "", tanggal: "" });
  };

  // Hapus Materi
  const handleDelete = async (id) => {
    if (!isAdmin) return;

    await deleteMateri(id);
    setMateri(materi.filter((m) => m.id !== id));
  };

  // Update Materi (opsional, bisa dipanggil dari tombol edit)
  const handleUpdate = async (id, updatedData) => {
    if (!isAdmin) return;

    await updateMateri(id, updatedData);
    setMateri(materi.map((m) => (m.id === id ? { ...m, ...updatedData } : m)));
  };

  return (
    <div className="space-y-6">
      {isAdmin && (
        <form
          onSubmit={handleAdd}
          className="p-4 bg-gray-100 text-gray-900 rounded-lg shadow space-y-3"
        >
          <h2 className="font-bold text-lg">Tambah Materi</h2>

          <input
            type="text"
            placeholder="Judul"
            value={form.judul}
            onChange={(e) => setForm({ ...form, judul: e.target.value })}
            className="w-full border p-2 rounded"
            required
          />

          <textarea
            placeholder="Isi Materi"
            value={form.isi}
            onChange={(e) => setForm({ ...form, isi: e.target.value })}
            className="w-full border p-2 rounded"
            required
          />

          <input
            type="date"
            value={form.tanggal}
            onChange={(e) => setForm({ ...form, tanggal: e.target.value })}
            className="w-full border p-2 rounded"
            required
          />

          <button
            type="submit"
            className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-800 cursor-pointer"
          >
            Tambah
          </button>
        </form>
      )}

      <ul className="space-y-3">
        {materi.map((m) => (
          <li
            key={m.id}
            className="p-4 bg-gray-100 text-gray-900 rounded-lg shadow flex justify-between items-center"
          >
            <div>
              <p className="font-semibold">{m.judul}</p>
              <p className="text-sm text-gray-600">{m.isi}</p>
              <p className="text-sm text-gray-500">Tanggal: {m.tanggal}</p>
            </div>

            {isAdmin && (
              <button
                onClick={() => handleDelete(m.id)}
                className="text-red-600 hover:underline cursor-pointer"
              >
                Hapus
              </button>
            )}
          </li>
        ))}
      </ul>
    </div>
  );
}
